import React, { useState } from 'react';
// @ts-ignore
import { ReactHashCalendar } from 'react-hash-calendar'
import dayjs from "dayjs";
import { List } from "antd-mobile";
import Ying from "../../../Ying";

const Calender: React.FC = () => {

    const dates = Ying.calender;
    const [date, setDate] = useState<Date>();

    return (
        <>
            <ReactHashCalendar
                model="inline"
                markDate={dates.map(item => ({
                    date: [item.date.format('YYYY/MM/DD')],
                    color: item.color,
                    type: item.type,
                }))}
                dateClickCallback={(date: Date | string) => {
                    if (date instanceof Date) {
                        setDate(date);
                    }
                }}
            />
            <List mode='card'>
                {dates.filter(item =>
                    item.date.isSame(dayjs(date), 'day')
                ).map(item =>
                    item.things.map(thing =>
                        <List.Item title={thing.title}>{thing.content}</List.Item>
                    )
                )}
            </List>
        </>
    );
};

export default Calender;
