import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate, useRoutes } from 'react-router-dom';
import { ErrorBlock, FloatingBubble } from "antd-mobile";
import CourseTable from "./pages/apps/courseTable/courseTable";
import Workspace from "./pages/workspace";
import { Home } from "@icon-park/react";
import Calender from "./pages/apps/calender";

function App() {

    const [display, setDisplay] = useState(false);
    const location = useLocation();
    const navigate = useNavigate();
    useEffect(() => {
        setDisplay(location.pathname !== '/');
    }, [location.pathname]);

    const routers = [
        {path: '/', element: <Workspace/>},
        {path: '/apps/courseTable', element: <CourseTable/>},
        {path: '/apps/calender', element: <Calender/>},
        {path: '*', element: <ErrorBlock status='empty' fullPage/>}
    ];

    return (
        <>
            {useRoutes(routers)}
            <>
                {
                    display ? (
                        <FloatingBubble
                            style={{
                                '--initial-position-bottom': '24px',
                                '--initial-position-right': '24px',
                                // '--edge-distance': '24px',
                            }}
                            onClick={() => navigate('/')}
                        >
                            <Home theme="outline" size="24" fill="#fff"/>
                        </FloatingBubble>
                    ) : null
                }
            </>
        </>

    );

}

export default App;
