import React, { CSSProperties, useState } from 'react';
import styles from "./index.less";
import dayjs from "dayjs";
import { AutoCenter, Card, Grid, NavBar } from "antd-mobile";
import Ying from "../../../Ying";

const format = "HH:mm";
const sections = [
    [dayjs('0000-00-00 08:00'), dayjs('0000-00-00 08:50')],
    [dayjs('0000-00-00 09:00'), dayjs('0000-00-00 09:50')],
    [dayjs('0000-00-00 10:10'), dayjs('0000-00-00 11:00')],
    [dayjs('0000-00-00 11:10'), dayjs('0000-00-00 12:00')],
    [dayjs('0000-00-00 14:30'), dayjs('0000-00-00 15:20')],
    [dayjs('0000-00-00 15:30'), dayjs('0000-00-00 16:20')],
    [dayjs('0000-00-00 16:30'), dayjs('0000-00-00 17:20')],
    [dayjs('0000-00-00 17:30'), dayjs('0000-00-00 18:20')],
];
const days = ['一', '二', '三', '四', '五', '六', '七'];

const CourseTable: React.FC = () => {

    const [width, setWidth] = useState<number>(0);
    const [height, setHeight] = useState<number>(0);
    const currentWeek = Math.ceil(dayjs().diff('2021-09-06', 'day') / 7);
    const courses = Ying.courses.filter(course => {
        for (let schedule of course.schedules) {
            if(schedule.weeks.indexOf(currentWeek) !== -1) {
                return true;
            }
        }
        return false;
    });

    return (
        <>
            <NavBar backArrow={false}>
                第 {currentWeek} 周
            </NavBar>
            <Grid columns={24} gap={6} className={styles.schedule}>
                <Grid.Item span={2}>
                    <Grid.Item>
                        <Card>
                            <AutoCenter>
                                {dayjs().month() + 1}
                            </AutoCenter>
                        </Card>
                    </Grid.Item>
                    <br/>
                    <Grid columns={1} gap={6}>
                        {sections.map((item, index) =>
                            <Grid.Item>
                                <div
                                    ref={element => {
                                        if (element) {
                                            setHeight(element.getBoundingClientRect().height);
                                        }
                                    }}
                                >
                                    <Card>
                                        <AutoCenter>
                                            {index}
                                        </AutoCenter>
                                        <AutoCenter>
                                            {item[0].format(format)}
                                        </AutoCenter>
                                        <AutoCenter>
                                            {item[1].format(format)}
                                        </AutoCenter>
                                    </Card>
                                </div>
                            </Grid.Item>
                        )}
                    </Grid>
                </Grid.Item>
                <Grid.Item span={22}>
                    <Grid columns={days.length} gap={6}>
                        {days.map(item =>
                            <Grid.Item>
                                <div
                                    ref={element => {
                                        if (element) {
                                            setWidth(element.getBoundingClientRect().width);
                                        }
                                    }}
                                >
                                    <Card>
                                        <AutoCenter>
                                            {item}
                                        </AutoCenter>
                                    </Card>
                                </div>
                            </Grid.Item>
                        )}
                    </Grid>
                    <br/>
                    <Grid columns={days.length} gap={6}>
                        {days.map((_, day) =>
                            <Grid.Item>
                                <Grid columns={1} gap={6}>
                                    {courses.map(item => {
                                        let schedule = undefined;
                                        item.schedules.forEach(item => {
                                            if (item.day === day) {
                                                schedule = item;
                                            }
                                        });
                                        if (!schedule) return null;

                                        // 这节课在今天的位置
                                        let index = -1;
                                        courses.forEach(course =>
                                            course.schedules.forEach(item => {
                                                if (item.day === day) index++;
                                            })
                                        );

                                        const fix = 6.04;
                                        // @ts-ignore
                                        const marginTop = (height + fix) * (schedule.start - index * 2);
                                        const style: CSSProperties = {
                                            width: width,
                                            // @ts-ignore
                                            height: height * (schedule.end - schedule.start) * 2 + fix,
                                            background: item.color,
                                            marginTop: marginTop > 0 ? marginTop : 0,
                                            wordWrap: 'break-word',
                                            padding: 0,
                                            color: 'white',
                                        };

                                        return (
                                            <Grid.Item>
                                                <Card style={style}>
                                                    <div
                                                        style={{
                                                            transform: 'scale(0.864)',
                                                        }}
                                                    >
                                                        {item.name}@{item.room}
                                                        <AutoCenter>
                                                            {item.teacher}
                                                        </AutoCenter>
                                                    </div>
                                                </Card>
                                            </Grid.Item>
                                        );
                                    })}
                                </Grid>
                            </Grid.Item>
                        )}
                    </Grid>
                </Grid.Item>
            </Grid>
        </>
    );
};

export default CourseTable;
